/**
 * @description
 * ## 
 * @module buttercam/console
 * */

/**
 * 
 * @example
 * import Debug from "buttercam/console"
 *
 * const debug = Debug('Debug Test');
 *
 * debug.log('this is a test'); // => [ Debug Test ] this is a test
 * debug.error('this is a test') // same
 * ...
 * */
class Console {
  
  /**
   * console
   * @param {string} scope - 
   * */
  constructor(scope) {
    
    /**
     * 
     * @type {string}
     * */
    this.scope = scope;
  
    /**
     * 
     * @type {object.<string, number>}
     * */
    this.timers = {};
  
    /**
     * 
     * @type {object.<string, string>}
     * */
    this.colorMap = {
      log: '#207806',
      warn: '#be981e',
      error: '#f01a1a',
      info: '#1041db',
      time: '#9397a6'
    };
  
    /**
     * 
     * @type {object.<string, function>}
     * */
    this.styles = {
      scope: (type) => {
        return `font-weight: bold;color: ${this.colorMap[type]}`
      }
    };
    
  }
  
  /**
   * 
   * @private
   * @param {string} type - , [log, warn, error, info] 
   * @param {*} [message=""] - 
   * @param {string} [extra=""] - scope 
   * @param {string} [style] - scope 
   * @returns {Console}
   * */
  _output(type, message = '', extra = '', style) {
    let {scope, styles} = this;
    try {
      // eslint-disable-next-line no-console
      console[type](`%c[ ${scope+extra} ] `, style || styles.scope(type), message)
    } catch (e) {
      // eslint-disable-next-line no-console
      console.log(`%c[ ${scope+extra} ] `, style || styles.scope('log'), message)
    }
    return this
  }
  
  /**
   * log
   * @param {*} message - 
   * @returns {Console}
   * */
  log(message) {
    return this._output('log', message)
  }
  
  /**
   * warn
   * @param {*} message - 
   * @returns {Console}
   * */
  warn(message) {
    return this._output('warn', message)
  }
  
  /**
   * error
   * @param {*} message - 
   * @returns {Console}
   * */
  error(message) {
    return this._output('error', message)
  }
  
  /**
   * info
   * @param {*} message - 
   * @returns {Console}
   * */
  info(message) {
    return this._output('info', message)
  }
  
  /**
   * 
   * @returns {Console}
   * */
  clear() {
    return this._output('clear')
  }
  
  /**
   * 
   * @param {string} scope - 
   * @returns {Console}
   * */
  time(scope) {
    if(!this.timers[scope]) {
      this.timers[scope] = (new Date()).getTime()
    } else {
      this.timers[scope] = (new Date()).getTime()
    }
    return this
  }
  
  /**
   * 
   * @param {string} scope - 
   * @returns {Console}
   * */
  timeEnd(scope) {
    let timer = this.timers[scope];
    let extra = ` - time - ${scope}`;
    if(!timer) {
      return this._output('warn', `${scope} is not defined`, extra)
    }
    return this._output('log', timer - (new Date()).getTime() + 'ms', extra, this.styles.scope('time'))
  }
  
}

export default Console

